/* Desktop */
@media only screen and (min-width: 900px) {
    #contact{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: relative;
        width: 100%;
        height: 90vh;
        background: #222;
        font-family: 'Oswald', sans-serif;
    }
    #contact::before{
        /* Steps for put the filter in the background img */
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: url("https://guilhermecf10.github.io/static/media/backgroundContact.4c61303a.jpg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        filter: brightness(50%);
    }
    #contact .background-title{
        width: 100%;
        display: flex;
        justify-content: center;
        position: absolute;
        top: 0px;
        font-size: 60px;
        color: rgba(119, 119, 119, 0.1);
    }
    #contact .background-title h1{
        -moz-user-select:none;
    }
    #contact .title{
        width: 100%;
        height: 140px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 30px;
        color: white;
    }
    #contact .title span{
        border-bottom: 2px solid #01fe87;
    }
    #contact .items{
        width: 100%;
        height: 100%;
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    #contact .items-content{
       
    }
    #contact .content-subtitle{
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        font-size: 26px;
    }
    #contact .content-info{
        display: flex;
        flex-direction: row;
        justify-content: center;
        text-align: justify;
        align-items: center;
        font-size: 20px;
    }
    #contact .content-info p{
        display: flex;
        align-items: center;
        min-width: 400px;
        color: rgb(119, 119, 119);
        border-bottom: 1px solid rgb(119, 119, 119);
        padding: 5px;
    }
    #contact .content-info span{
        margin-right: 15px;
        color: white;
    }
    #contact .content-social{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        margin-top: 60px;
    }
    #contact .content-social a img{
        min-width: 50px;
        min-height: 50px;
        margin-right: 20px;
        margin-top: 20px;
        border-radius: 20px;
        animation: social-icons-contact 2s linear infinite;
    }
    @keyframes social-icons-contact {
        0%, 100%{
            opacity: 1;
        }
        50%{
            opacity: 0.5;
        }
    }
}


