/* Desktop */
@media only screen and (min-width: 900px) {
    #services{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: relative;
        width: 100%;
        height: 100vh;
        background: #333;
        font-family: 'Roboto Mono', monospace;
        
    }
    #services .background-title{
        width: 100%;
        display: flex;
        justify-content: center;
        position: absolute;
        top: 0px;
        font-size: 60px;
        color: rgba(119, 119, 119, 0.1);
    }
    #services .background-title h1{
        -moz-user-select:none;
    }
    #services .title{
        width: 100%;
        height: 140px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 30px;
        color: white;
    }
    #services .title span{
        border-bottom: 2px solid rgb(1, 254, 135);
    }
    #services .card{
        width: 100%;
        height: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 30px;
        
    }
    #services .card::before{
         /* Steps for put the filter in the background img */
         content: "";
         position: absolute;
         top: 0;
         left: 0;
         display: flex;
         width: 100%;
         height: 100%;
         border-radius: 200px;
         background-image: url("https://guilhermecf10.github.io/static/media/backgroundServicesDesk.1ff72737.jpg");
         background-size: cover;
         background-position: center center;
         background-repeat: no-repeat;
         filter: brightness(50%);
    }
    #services .card-line{
        position: relative;
        width: 85%;
        display: flex;
        flex-direction: row;
    }
    #services .card-line-item{
        width: 50%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        padding: 20px;
        border-radius: 20px;
        margin-bottom: 20px;
        margin-right: 10px;
        transition: all 1s linear;
        background: rgba(51, 51, 51, 1);
        transform: scale(0.9);
    }
    #services .card-line-item:hover{
        transform: scale(1);
        border: 1px solid #01fe87;
        color: white;
    }
    #services .card-line-item-icon{
        width: 10%;
        margin-right: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    #services .card-line-item-icon span{
        background-color: rgb(119, 119, 119);
        padding: 10px;
        border-radius: 20px;
    }
    #services .card-line-item-content{
        max-width: 80%;
    }
    #services .card-line-item-content h3{
        color: white;
    }
    #services .card-line-item-content span{
        color: rgb(119, 119, 119);
    }

    #services .scroll{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 20px;
        width: 100%;
        height: 60px;
    }
    #services .scroll a{
        position: relative;
        color: white;
        animation: scroll-down-services 2s ease-in-out infinite;
    }
    #services .scroll a span{
        font-size: 40px;
    }
    @keyframes scroll-down-services {
        from{
            transform: translateY(-20px);
        }
        to{
            transform: translateY(0px);
        }
    }
    
    
}


