/* Desktop */
@media only screen and (min-width: 900px) {
    #about{
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        width: 100%;
        height: 100vh;
        background: #222;
        font-family: 'Roboto Mono', monospace;
    }
    #about .background-title{
        width: 100%;
        display: flex;
        justify-content: center;
        position: absolute;
        top: 0px;
        font-size: 60px;
        color: rgba(119, 119, 119, 0.1);
    }
    #about .background-title h1{
        -moz-user-select:none;
    }
    #about .title{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 140px;
        font-size: 30px;
        color: white;
    }
    #about .title span{
        border-bottom: 2px solid #01fe87;
    }
    #about .items{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
    #about .items-content{
        width: 70%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: justify;
    }
    #about .content-subtitle{
        max-width: 90%;
        color: white;
        font-size: 16px;
    }
    #about .content-subtitle span{
        color: rgb(1, 254, 135);
    }
    #about .content-text{
        margin-top: 30px;
        color: rgb(119, 119, 119);
        max-width: 90%;
        font-size: 18px;
    }
    #about .items-info{
        width: 30%;
        display: flex;
        flex-direction: column;
        font-size: 0.88rem;
    }
    #about .info-section{
        max-width: 90%;
        border-bottom: 1px solid rgb(119, 119, 119);
    }
    #about .info-section p{
        color: white;
    }
    #about .info-section span{
        color: rgb(179, 179, 179);
    }
    #about .info-button{
        display: flex;
        justify-content: center;
        margin-top: 20px;    
    }
    #about .info-button a{
        width: 80%;
        background-color: rgb(1, 254, 135);
        border: none;
        padding: 20px;
        border-radius: 20px;
        color: black;
        transition: all 0.5s linear;
        font-size: 18px;
        font-family: 'Roboto Mono', monospace;
        text-align: center;
    }
    #about .info-button a:hover{
        scale: 1.1;
        background-color: rgba(1, 254, 135, 0.5);
        color: white;
    }

    #about .scroll{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 60px;
    }
    #about .scroll a{
        position: relative;
        color: white;
        animation: scroll-down-about 2s ease-in-out infinite;
    }
    #about .scroll a span{
        font-size: 40px;
    }
    @keyframes scroll-down-about {
        from{
            transform: translateY(-20px);
        }
        to{
            transform: translateY(0px);
        }
    }
}


/* Cellphones */
@media only screen and (max-width: 899px) {
    #about{
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        width: 100%;
        min-height: 100vh;
        background: #222;
        font-family: 'Roboto Mono', monospace;
        padding: 10px 0px;
    }
    #about .background-title{
        width: 100%;
        display: flex;
        justify-content: center;
        position: absolute;
        top: 10px;
        font-size: 40px;
        color: rgba(119, 119, 119, 0.1);
    }
    #about .background-title h1{
        -moz-user-select:none;
    }
    #about .title{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 10vh;
        font-size: 23px;
        color: white;
    }
    #about .title span{
        border-bottom: 2px solid #01fe87;
    }
    #about .items{
        width: 100%;
        height: 80vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    #about .items-content{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: justify;
    }
    #about .content-subtitle{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 20px;
        width: 90%;
        color: white;
        font-size: 12px;
    }
    #about .content-subtitle span{
        color: rgb(1, 254, 135);
    }
    #about .content-text{
        margin-top: 20px;
        color: rgb(119, 119, 119);
        width: 85%;
        font-size: 16px;
    }
    #about .items-info{
        margin-top: 20px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 0.88rem;
    }
    #about .info-section{
        width: 80%;
        border-bottom: 1px solid rgb(119, 119, 119);
    }
    #about .info-section p{
        color: white;
    }
    #about .info-section span{
        color: rgb(179, 179, 179);
    }
    #about .info-button{
        width: 100%;
        display: flex;
        justify-content: center;
        margin-top: 30px;
        margin-bottom: 20px;
    }
    #about .info-button a{
        width: 70%;
        background-color: rgb(1, 254, 135);
        border: none;
        padding: 20px;
        border-radius: 20px;
        color: black;
        transition: all 0.5s linear;
        font-size: 18px;
        font-family: 'Roboto Mono', monospace;
        text-align: center;
    }
    #about .info-button a:hover{
        scale: 1.1;
        background-color: rgba(1, 254, 135, 0.5);
        color: white;
    }
    #about .scroll{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 60px;
    }
    #about .scroll a{
        position: relative;
        color: white;
        animation: scroll-down 2s ease-in-out infinite;
    }
    #about .scroll a span{
        font-size: 40px;
    }
    @keyframes scroll-down {
        from{
            margin-top: -30px;
        }
        to{
            margin-top: 30px;
        }
    }
    
}







