/* Desktop */
@media only screen and (min-width: 900px) {
    /* Main */
    .main{
        width: 80%;
        min-height: 90vh; 
        margin-left: 20%;
    }

    /* Main Items */
    .main-items{
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

}


/* Cellphones */
@media only screen and (max-width: 899px) {
    /* Main */
    .main{
        width: 100%;
        min-height: 90vh; 
    }

    /* Main Items */
    .main-items{
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

}
