/* Desktop */
@media only screen and (min-width: 900px) {
    .footer{
        width: 100%;
        height: 10vh;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #333;
        font-family: 'Roboto Mono', monospace;
    }
    
    .footer-items{}
    
    .footer .credits{
        color: white;
    }
    
    .footer .credits span{
        color: #01fe87;
    }
}


/* Cellphones */
@media only screen and (max-width: 899px) {
    .footer{
        width: 100%;
        height: 10vh;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #333;
        font-family: 'Roboto Mono', monospace;
    }
    
    .footer-items{}
    
    .footer .credits{
        font-size: 10px;
        color: white;
    }
    
    .footer .credits span{
        color: #01fe87;
    }
}









