/* Desktop */
@media only screen and (min-width: 900px) {
    
    /* Main Header */
    .header{
        position: fixed;
        display: flex;
        width: 20%;
        height: 100%;
        background: #111418;
        font-family: 'Magra', sans-serif;
    }

    /* Header Items */
    .header-items{
        width: 100%;
        height: 100%;
    }

    /* Img and Name Box */
    .header-items-user{
        width: 100%;
        height: 40vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .header-items-user-img{
        display: flex;
        align-items: center;
        background: #343a40;
        padding: 10px;
        border-radius: 100px;
    }

    .header-items-user-img img{
        border-radius: 100px;
    }

    .header-items-user-name{
        font-size: 23px;
        margin-top: 20px;
        color: white;
    }

    /* Nav */
    .header-items-nav{
        width: 100%;
        height: 50vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 19px;
    }

    /* Ul List */
    .header-items-nav-ul{
        width: 100%;
        list-style-type: none;
        margin: 0;
        padding: 0;

    }

    /* Li Item */
    .header-items-nav-ul-item{
        min-height: 50px;
        display: flex;
        justify-content: center;
        text-align: center;
        transition: all 1s linear;
    }

    .header-items-nav-ul-item:hover{
        scale: 1.1;
    }

    /* <a> Link */
    .header-items-nav-ul-item a{
        text-decoration: none;
        color: #aab1b8; 
        transition: all 0.1s linear;
    }

    .header-items-nav-ul-item a:hover{
        color: #01fe87;
    }
    .header-items-nav-ul-item a:active{
        color: #01fe87;
    }

        
                
    /* Social Networks Ul */
    .header-items-ul{
        width: 100%;
        height: 10vh;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        list-style-type: none;
    }

    /* Li item */
    .header-items-ul-social{
        margin: 0px 10px 0px 10px;
        transition: all 1s linear;
    }

    .header-items-ul-social:hover{
        scale: 1.3;
    }

    /* <a> Link */
    .header-items-ul-social a{
        text-decoration: none;
        color: #aab1b8;
        font-size: 20px;
    }

}



/* Cellphones */
@media only screen and (max-width: 899px) {
    
    /* Main Header */
    .header{
        display: flex;
        width: 100%;
        height: 10vh;
        background: #111418;
        position: relative;
        /* overflow: hidden; */
    }

    /* Header Items */
    .header-items{
        display: flex;
        width: 100%;
        height: 100%;
    }

    /* Img and Name Box */
    .header-items-user{
        margin-left: 20px;
        width: 80%;
        height: 100%;
        display: flex;
        flex-direction: row;
    }
    .header-items-user-img{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .header-items-user-img img{
         max-width: 48px;
         max-height: 48px;
         border-radius: 100px;
    }
    .header-items-user-name{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content:start;
        align-items: center;
        color: white;
        font-size: 20px;
    }
    .header-items-user-name span{
        margin-left: 10px;
    }


    /* Nav */
    .header-items-nav{      
        position: absolute;
        right: 0;
        top: 10vh;
        width: 70%;
        background: #111418;
        font-size: 19px;
        border-radius: 0 0 0 20px;
        transition: all 1s linear;
        z-index: 1;
    }
    /* Ul List */
    .header-items-nav-ul{
        width: 100%;
        list-style-type: none;
        margin: 0;
        padding: 0;
    }
    /* Li Item */
    .header-items-nav-ul-item{
        min-height: 50px;
        display: flex;
        justify-content: center;
        text-align: center;
        transition: all 1s linear;
    }
    .header-items-nav-ul-item:hover{
        scale: 1.1;
    }
    /* <a> Link */
    .header-items-nav-ul-item a{
        text-decoration: none;
        color: #aab1b8; 
        transition: all 0.1s linear;
    }
    .header-items-nav-ul-item a:hover{
        color: #01fe87;
    }
    .header-items-nav-ul-item a:active{
        color: #01fe87;
    }


    /* Social Networks Ul */
    .header-items-ul{
        position: absolute;
        top: 15vh;
        left: 20%;
        width: 10%;
        background: #111418;
        padding: 10px;
        border-radius: 20px 0 0 20px;
        list-style-type: none;
        transition: all 1s linear;
        z-index: 1;
    }
    /* Li item */
    .header-items-ul-social{
        margin: 15px;
        display: flex;
        justify-content: center;
        transition: all 1s linear;
    }
    .header-items-ul-social:hover{
        scale: 1.3;
    }
    /* <a> Link */
    .header-items-ul-social a{
        text-decoration: none;
        color: #aab1b8;
        font-size: 20px;
    }


    .header-items-button{
        width: 20%;
        color: white;
        background-color: rgba(0,0,0,0);
        border: none;
    }

}






