/* Desktop */
@media only screen and (min-width: 900px) {
    #home{
        display: flex;
        flex-direction: column;
        position: relative;
        width: 100%;
        height: 100vh;
        font-family: 'Oswald', sans-serif;
    }
    #home::before{
        /* Steps for put the filter in the background img */
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: url("https://guilhermecf10.github.io/static/media/backgroundHome.7d6d3fcb.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        filter: brightness(50%);
    }
    #home .home-title{
        width: 100%;
        height: 30%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        font-size: 36px;
        color: white;
    }
    #home .home-subtitle-name{
        width: 100%;
        height: 20%;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    #home .home-subtitle-name h2{
        position: relative;
        font-size: 60px;
        color: rgba(37, 40, 57, 0.0);
        -webkit-text-stroke: 0.3vw rgba(56, 61, 82, 0.0);
    } 
    #home .home-subtitle-name h2::before{
        content: attr(data-text);
        position: absolute;
        top: 0;
        left: 0;
        width: 0%;
        height: 100%;
        color: white;
        overflow: hidden;
        animation: name 12s ease-in-out infinite;
        -webkit-text-stroke: 0vw #383d52;
        white-space: nowrap;
    }
    @keyframes name {
        0%, 10%, 86%, 90%{
            width: 0;
            border-right: 0px solid white;
        }
        30%{
            width: 100%;
            border-right: 2px solid white;
        }
        31%,60%{
            width: 100%;
            border-right: 0px solid white;
        }
        85%{
            border-right: 2px solid white;
        }
    }
    #home .home-subtitle-info{
        width: 100%;
        height: 10%;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    #home .home-subtitle-info h2{
        position: relative;
        color: rgba(37, 40, 57, 0.0);
        -webkit-text-stroke: 0.3vw rgba(56, 61, 82, 0.0);
        font-size: 30px;
    }
    #home .home-subtitle-info h2::before{
        content: attr(data-text);
        position: absolute;
        top: 0;
        left: 0;
        width: 0%;
        height: 100%;
        color: white;
        border-right: 2px solid white;
        overflow: hidden;
        animation: info 12s ease-in-out infinite;
        -webkit-text-stroke: 0vw #383d52;
        white-space: nowrap;
    }
    @keyframes info {
        0%, 10%, 30%, 100%{
            width: 0;
            border-right: 0px solid white;
        }
        
        40%{
            border-right: 2px solid white;
        }
        60%, 85%{
            width: 100%;
            border-right: 0px solid white;
        }
        91%{
            border-right: 2px solid white;
        }
    }
    #home .home-button{
        width: 100%;
        height: 30%;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center; 
    }
    #home .home-button a{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 15vw;
        height: 10vh;
        color: #01fe87;
        border: 1px solid rgba(1, 254, 135, 1);
        padding: 20px;
        background-color: rgba(0,0,0,0);
        border-radius: 20px; 
        transition: all 0.6s linear;
        font-size: 24px;
        font-family: 'Oswald', sans-serif;
        animation: button-anim 5s ease-in-out infinite;
    }
    @keyframes button-anim {
        0%, 100%{
            color: #01fe87;
            background-color: rgba(0,0,0,0);
        }
        50%{
            color: white;
            background-color: rgba(1, 254, 135, 0.3);
        }
    }
    #home .home-button a:hover{
        transform: scale(1.1);
        background-color: rgba(1, 254, 135, 0.5);
        color: white;
    }
    #home .home-scroll{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 10%;
    }
    #home .home-scroll a{
        position: relative;
        color: white;
        animation: scroll-down 2s ease-in-out infinite;
    }
    #home .home-scroll a span{
        font-size: 40px;
    }
    @keyframes scroll-down {
        from{
            margin-top: -50px;
        }
        to{
            margin-top: 30px;
        }
    }
    
}

