/* Desktop */
@media only screen and (min-width: 900px) {
    #resume{
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        width: 100%;
        min-height: 90vh;
        background: #222;
        font-family: 'Roboto Mono', monospace;
        padding: 20px 0px;
    }
    #resume .background-title{
        width: 100%;
        display: flex;
        justify-content: center;
        position: absolute;
        top: 20px;
        font-size: 60px;
        color: rgba(119, 119, 119, 0.1);
    }
    #resume .background-title h1{
        -moz-user-select:none;
    }
    #resume .title{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 120px;
        font-size: 30px;
        color: white;
    }
    #resume .title span{
        border-bottom: 2px solid #01fe87;
    }
    #resume .card{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    #resume .card-line{
        width: 100%;
        margin-top: 20px;
        margin-bottom: 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
    }
    #resume .card-line ul{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        width: 100%;
    }
    #resume .card-line ul li{
        width: 45%;
    }
    #resume .card-line-title{
        color: white;
        font-size: 20px;;
    }
    #resume .card-line-item{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        padding: 20px;
        border-radius: 20px;
        margin-bottom: 20px;
        transition: all 1s linear;
    }
    #resume h3{
        margin: 5px 0px 5px 0px;
    }
    #resume .content-date{
        color: white;
    }
    #resume .content-date span{
        background-color: rgba(1, 254, 135, 0.6);
        padding: 2px;
    }
    #resume .content-stage{
        color: white;
    }
    #resume .content-name{
        color: rgba(1, 254, 135, 0.6);
    }
    #resume .content-info{
        color: rgb(119, 119, 119)
    }
    #resume .card-line-item-icon{
        width: 100px;
        margin-right: 20px;
    }
    #resume .card-line-item-icon img{
        width: 100%;
        height: 100%;
        border-radius: 20px;
    }
    #resume .card-line-item-content{
        width: 80%;
        display: flex;
        flex-direction: column;
    }
    
    #resume .scroll{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 60px;
    }
    #resume .scroll a{
        position: relative;
        color: white;
        animation: scroll-down 2s ease-in-out infinite;
    }
    #resume .scroll a span{
        font-size: 40px;
    }
    @keyframes scroll-down {
        from{
            transform: translateY(-10px);
        }
        to{
            transform: translateY(20px);
        }
    }
}


